@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville:400,400i,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media screen and (min-width: 700px) {
  .App {
    max-width: 80%;
    margin: 0 auto; } }

header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 50px 10px; }
  @media screen and (min-width: 700px) {
    header {
      flex-direction: row;
      margin: 0 auto; } }
  header h1 {
    margin: 0;
    font-size: 40px; }
  header a {
    font-family: 'Libre Baskerville', serif;
    font-weight: bold;
    color: #000;
    text-decoration: none; }
    header a:visited {
      color: #000; }
  header nav {
    margin: 20px 0 0 0;
    font-size: 20px; }
    @media screen and (min-width: 700px) {
      header nav {
        margin: 0; } }
    header nav a {
      margin: 0 20px; }
      @media screen and (min-width: 700px) {
        header nav a {
          margin: 0 0 0 50px; } }

.home-page .home-links {
  margin: 0 auto; }
  @media screen and (min-width: 700px) {
    .home-page .home-links {
      display: flex; } }

.home-page .home-link {
  text-decoration: none;
  height: 150px;
  display: block;
  background-size: cover; }
  @media screen and (min-width: 700px) {
    .home-page .home-link {
      width: 50%;
      height: 700px; } }
  .home-page .home-link p {
    color: #fff;
    font-family: 'Libre Baskerville', serif;
    font-size: 30px;
    font-style: italic;
    text-align: center;
    padding: 100px 0;
    margin: 0; }
  .home-page .home-link--mens {
    background-image: url(/images/home-man.jpg); }
  .home-page .home-link--womens {
    background-image: url(/images/home-woman.jpg); }
  .home-page .home-link div {
    height: 150px; }
    @media screen and (min-width: 700px) {
      .home-page .home-link div {
        height: 700px; } }

.list-of-products {
  list-style: none;
  padding: 0; }
  .list-of-products li {
    margin: 0 0 50px 0;
    cursor: pointer; }
    @media screen and (min-width: 700px) {
      .list-of-products li {
        display: inline-block;
        width: 25%;
        padding: 0 10px;
        box-sizing: border-box; } }
    .list-of-products li img {
      width: 100%; }
  .list-of-products__name {
    font-family: 'Libre Baskerville', serif;
    font-size: 30px;
    text-align: center;
    margin: 10px 0 0 0; }
  .list-of-products__price {
    font-family: 'Libre Baskerville', serif;
    font-size: 20px;
    font-style: italic;
    text-align: center;
    margin: 0; }

.popup {
  position: absolute;
  top: 20px;
  left: 0;
  max-height: 500px;
  width: 100%; }
  @media screen and (min-width: 700px) {
    .popup {
      top: 50px; } }
  .popup__inner {
    background-color: #ccc;
    margin: 0 auto;
    height: 100%;
    width: 90%;
    max-width: 1000px;
    box-shadow: 0px 5px 31px 5px rgba(0, 0, 0, 0.38); }
    @media screen and (min-width: 700px) {
      .popup__inner {
        max-width: 1000px;
        display: flex;
        align-items: flex-start;
        width: 70%; } }
  .popup img {
    width: 100%;
    height: 100%;
    display: block; }
    @media screen and (min-width: 700px) {
      .popup img {
        width: 60%; } }
  .popup__text {
    padding: 20px;
    display: flex;
    flex-direction: column;
    font-family: 'Libre Baskerville', serif;
    font-size: 30px;
    text-align: left; }
    @media screen and (min-width: 700px) {
      .popup__text {
        width: 40%;
        padding: 40px; } }
  .popup__close {
    font-style: italic;
    text-decoration: underline;
    text-align: right;
    font-size: 20px;
    cursor: pointer; }
  .popup__name {
    font-size: 50px;
    margin: 0; }
  .popup__price {
    margin: 0; }
    @media screen and (min-width: 700px) {
      .popup__price {
        margin: 20px 0; } }
  .popup__desc {
    font-size: 15px; }

